.footer__input {
  position: relative;
  width: calc((100% - 30px) / 2);
}
@media (max-width: 1365px) {
  .footer__input {
    width: calc((100% - 59px) / 2);
  }
}
@media (max-width: 767px) {
  .footer__input {
    width: 100%;
  }
}

.footer__input .input-message {
  font-size: 14px;
  line-height: 22px;
  font-weight: 400;
  color: #ff1553;
  position: absolute;
  bottom: 15px;
  left: 0;
}
@media (max-width: 767px) {
  .footer__input .input-message {
    bottom: 4px;
  }
}

.footer__free input {
  font-size: 14px;
  line-height: 22px;
  font-weight: 400;
  font-family: inherit;
  width: 100%;
  border: none;
  border-bottom: 1px solid #74819c;
  background-color: transparent;
  color: #ffffff;
  padding: 11px 0 3px;
  margin: 0 0 37px;
}
.footer__free input::placeholder {
  color: #ffffff;
}
@media (max-width: 1365px) {
  .footer__free input {
    font-size: 16px;
    line-height: 24px;
    font-weight: 300;
    padding: 11px 0 4px;
    margin-bottom: 27px;
  }
}
@media (max-width: 767px) {
  .footer__free input {
    font-size: 14px;
    line-height: 22px;
    font-weight: 300;
    margin-bottom: 23px;
    padding: 12px 0 4px;
  }
}
.footer__free input:hover {
  border-color: #ffffff;
}
.footer__free input:focus {
  border-color: rgba(255, 255, 255, 0);
  outline: 1px solid #ffffff;
  outline-offset: 1px;
}

.footer__form-button {
  font-size: 12px;
  line-height: 12px;
  font-weight: 700;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  width: 100%;
  padding: 27px 0;
  border: none;
  outline: 2px solid #ffffff;
  outline-offset: -2px;
  display: block;
  text-align: center;
}
.footer__form-button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (max-width: 1365px) {
  .footer__form-button {
    padding: 22px 0;
    font-weight: 700;
  }
}
@media (max-width: 767px) {
  .footer__form-button {
    margin-top: 15px;
  }
}

.footer__contacts {
  width: 50%;
  padding: 60px 0 64px 120px;
  border-top: 1px solid #74819c;
}
@media (max-width: 1365px) {
  .footer__contacts {
    width: 100%;
    padding: 104px 133px 100px;
    border: none;
  }
}
@media (max-width: 767px) {
  .footer__contacts {
    width: 100%;
    padding: 66px 15px 70px;
  }
}

.footer__contacts-wrapper {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 15px 120px;
  position: relative;
}
@media (max-width: 1365px) {
  .footer__contacts-wrapper {
    gap: 29px 141px;
  }
}
@media (max-width: 767px) {
  .footer__contacts-wrapper {
    gap: 28px 141px;
  }
}
.footer__contacts-wrapper::before {
  content: "";
  width: 1px;
  height: 117px;
  background-color: #74819c;
  position: absolute;
  top: 0;
  left: 220px;
}
@media (max-width: 1365px) {
  .footer__contacts-wrapper::before {
    left: 251px;
    height: 137px;
  }
}
@media (max-width: 767px) {
  .footer__contacts-wrapper::before {
    display: none;
  }
}

.footer__contacts-item {
  min-width: 170px;
}
@media (max-width: 767px) {
  .footer__contacts-item--schedule {
    order: 1;
  }
  .footer__contacts-item--phone {
    order: 2;
  }
  .footer__contacts-item--email {
    order: 3;
  }
}

.footer__contacts-title {
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  color: #102654;
  margin: 0 0 5px;
}
@media (max-width: 1365px) {
  .footer__contacts-title {
    font-size: 22px;
    line-height: 22px;
    font-weight: 400;
    margin-bottom: 11px;
  }
}
@media (max-width: 767px) {
  .footer__contacts-title {
    font-size: 20px;
    line-height: 20px;
    font-weight: 400;
    margin-bottom: 12px;
  }
}

.footer__contacts-text {
  font-size: 14px;
  line-height: 22px;
  font-weight: 400;
  color: #102654;
  margin: 0;
}
@media (max-width: 767px) {
  .footer__contacts-text {
    line-height: 20px;
  }
}

.footer__links {
  background-color: #030a1a;
  padding: 39px 0 40px;
}
@media (max-width: 1365px) {
  .footer__links {
    padding: 45px 0;
  }
}
@media (max-width: 767px) {
  .footer__links {
    padding: 29px 0 45px;
  }
}

.footer__links .container {
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .footer__links .container {
    flex-direction: column;
  }
}

.footer__logo {
  flex-shrink: 0;
  flex-grow: 0;
  margin-right: 257px;
}
@media (max-width: 1365px) {
  .footer__logo {
    margin-right: 44px;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    align-self: flex-start;
    margin: 0 0 30px;
  }
}

.footer__nav {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}
@media (max-width: 1365px) {
  .footer__nav {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .footer__nav {
    gap: 18px 35px;
    margin-bottom: 53px;
  }
}

.footer__link {
  font-size: 14px;
  line-height: 22px;
  font-weight: 400;
  display: block;
  color: #ffffff;
  padding-right: 1px;
  position: relative;
}
.footer__link::before {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  content: "";
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  display: none;
}
.footer__link:hover::before {
  display: block;
}
.footer__link:focus::before {
  display: block;
  bottom: initial;
  top: 50%;
  transform: translate(-50%, -50%);
  width: calc(100% + 20px);
  height: 32px;
  border: 1px solid #ffffff;
  background-color: transparent;
}
.footer__link:active::before {
  display: none;
}
.footer__link:disabled, .footer__link.disabled {
  opacity: 0.4;
}

.footer__socials {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin-left: auto;
  gap: 10px;
}
@media (max-width: 767px) {
  .footer__socials {
    align-self: center;
    margin: 0 auto;
  }
}

.footer__socials-link {
  width: 25px;
  height: 25px;
  background-color: #ffffff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__socials-link:hover, .footer__socials-link:focus {
  background-color: #103aae;
}
.footer__socials-link:active {
  background-color: #102654;
}

.footer__socials-link:hover svg,
.footer__socials-link:focus svg {
  color: #ffffff;
}

